.Search{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.SearchBar{
    flex: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.SearchBar form{
    display: flex;
    width: 50%;
    justify-content: space-evenly;
}

.SearchBar form input{
    border: none;
    border-bottom: 2px solid #1f25d1;
    outline: none;
    width: 250px;
}

.SearchBar form select{
    width: 150px;
    outline: none;
    border: none;
    border-bottom: 2px solid #1f25d1;
}
.filters{
    margin-top: 30px;
    width: 40%;
    display: flex;
    flex: 40%;
    justify-content: space-evenly;
}